.button {
  border-radius: 3px;
  padding: 5px 24px;
  border: none;
  font-weight: 600;
  font-size: 14px;
}

.disabled {
  /*background: #004d99;*/
  opacity: 0.5;
}

.secondary {
  border: 1px solid #06c;
  color: #06c;
}

.default {
  background: #06c;
  color: #fff;
}

.red {
  background: #cc1439;
  color: #fff;
}

.default:hover {
  cursor: pointer;
  background: #0080ff;
}

.red:hover {
  cursor: pointer;
  background: #ff1a47;
}

.secondary:hover {
  cursor: pointer;
  background: #e5f0fa;
}


.default:active {
  background: #0059b2;
}

.secondary:active {
  background: #c9e5ff;
}

.red:active {
  background: #b21232;
}

.button:disabled {
  background: #004d99;
  opacity: 0.5;
}
