.friendMessage {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
}

.friendImageAndText {
  display: flex;
  gap: 10px;
}

.friendImageAndText img {
  width: 48px;
  height: 48px;
}

.friendText {
  padding: 8px 13px;
  background-color: #F5F7FB;
  box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
  border-radius: 10px 10px 0 10px;
}

.friendText:after {

}

.friendName {
  font-weight: 600;
  font-size: 16px;
  color: #000;
}

.friendMessageText {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #000;
}

.friendTime {
  padding-left: 10px;
  margin-top: 5px;
  font-weight: 600;
  font-size: 10px;
  color: #000;
}
